import React from "react";
import {
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Button,
  FormGroup,
  Row,
  Label,
} from "reactstrap";
import { useState } from "react";
import "./AddServiceAccountModal.scss";
import { FormInputCorrected } from "components/Form";
import { useEffect } from "react";

const emailFormat = /^[a-zA-Z0-9_\-.]+@[a-zA-Z0-9_\-.]+\.[a-zA-Z0-9]{2,}$/;

function AddServiceAccountModal(props) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [repeatPassword, setRepeatPassword] = useState("");
  const [type, setType] = useState("");
  const [isFormReady, setIsFormReady] = useState({});
  const [errors, setErrors] = useState({
    email: [],
    password: [],
    repeatPassword: [],
  });

  useEffect(() => {
    setEmail("");
    setPassword("");
    setRepeatPassword("");
    setType("");
    clearErrors();
  }, [props.isOpen]);

  const submitServiceAccount = (e) => {
    e.preventDefault();
    if (isValid()) {
      props.onAdd({ email, password, type });
    }
  };

  const isValid = () => {
    let valid = true;
    const errorList = { email: [], password: [], repeatPassword: [] };
    if (email === "") {
      errorList.email.push("This field cannot be blank");
      valid = false;
    } else if (!email.match(emailFormat)) {
      errorList.email.push("Please input a valid email");
      valid = false;
    }
    if (password === "") {
      errorList.password.push("This field cannot be blank");
      valid = false;
    }
    if (repeatPassword === "") {
      errorList.repeatPassword.push("This field cannot be blank");
      valid = false;
    } else if (password !== repeatPassword) {
      errorList.repeatPassword.push("Passwords don't match");
      valid = false;
    }

    setErrors(errorList);
    return valid;
  };

  const clearErrors = (field) => {
    setErrors({ ...errors, [field]: [] });
  };

  const handleError = (field, status) => {
    if (status) {
      let check = true;
      for (let key in isFormReady) {
        if (key !== field && key !== "check") {
          check = check && isFormReady[key];
        }
      }
      setIsFormReady({ ...isFormReady, [field]: true, check: check });
    } else {
      setIsFormReady({ ...isFormReady, [field]: false, check: false });
    }
  };

  const setEmailData = (data) => {
    clearErrors("email");
    setEmail(data);
  };

  const setPasswordData = (data) => {
    clearErrors("password");
    setPassword(data);
  };

  const setRepeatPasswordData = (data) => {
    clearErrors("repeatPassword");
    setRepeatPassword(data);
    if (data !== password) {
      setErrors({ ...errors, repeatPassword: ["Passwords don't match"] });
    }
  };
  return (
    <Modal isOpen={props.isOpen} toggle={() => props.toggle()}>
      <ModalHeader>Add Service Account Modal</ModalHeader>
      <form
        onSubmit={(e) => submitServiceAccount(e)}
        className="service-account-modal-body"
      >
        <ModalBody>
          <FormGroup>
            <Row>
              <Label className="form-control-label" htmlFor="sa-email">
                Type
              </Label>
            </Row>
            <Row className="d-block">
              <select
                id="sa-type"
                value={type}
                onChange={(e) => setType(e.target.value)}
                required
              >
                <option value="" disabled>
                  --Choose type--
                </option>
                <option value="kiosk">Kiosk</option>
                <option value="welcome_board">Welcome Board</option>
              </select>
            </Row>
          </FormGroup>
          <FormGroup>
            <Row>
              <Label className="form-control-label" htmlFor="sa-email">
                Email
                <sup className="text-required">*</sup>
              </Label>
            </Row>
            <Row className="d-block">
              <FormInputCorrected
                type="email"
                id="sa-email"
                name="email"
                handleError={handleError}
                errors={errors["email"]}
                value={email}
                onChange={(e) => setEmailData(e.target.value)}
                required={true}
              />
            </Row>
          </FormGroup>
          <FormGroup>
            <Row>
              <Label className="form-control-label" htmlFor="sa-pas">
                Password
                <sup className="text-required">*</sup>
              </Label>
            </Row>
            <Row className="d-block">
              <FormInputCorrected
                type="password"
                id="sa-pass"
                name="password"
                handleError={handleError}
                errors={errors["password"]}
                value={password}
                onChange={(e) => setPasswordData(e.target.value)}
                required={true}
              />
            </Row>
          </FormGroup>
          <FormGroup>
            <Row>
              <Label className="form-control-label" htmlFor="sa-r-pass">
                Repeat password
                <sup className="text-required">*</sup>
              </Label>
            </Row>
            <Row className="d-block">
              <FormInputCorrected
                type="password"
                id="sa-r-pass"
                name="r-pass"
                handleError={handleError}
                errors={errors["repeatPassword"]}
                onChange={(e) => setRepeatPasswordData(e.target.value)}
                value={repeatPassword}
                required={true}
              />
            </Row>
          </FormGroup>
        </ModalBody>
        <ModalFooter>
          <Button color="link" onClick={(e) => props.toggle()}>
            Cancel
          </Button>
          <Button color="success" type="submit">
            <i className="fas fa-plus" /> Add
          </Button>
        </ModalFooter>
      </form>
    </Modal>
  );
}

export default AddServiceAccountModal;
