
import React from "react";
import {
  Button,
  Card,
  CardHeader,
  Container,
  Table,
  UncontrolledTooltip,
  Nav,
  NavItem,
  NavLink,
  Row,
  TabContent,
  TabPane,
} from "reactstrap";
import { Link } from "react-router-dom";
import classnames from "classnames";
import adminApi from '../../../api/adminApi';
import serviceAccountApi from '../../../api/serviceAccountApi';
import systemSettingsApi from '../../../api/systemSettingsApi';
import { FormInput } from 'components/Form';
import './System.scss';
import avatar from '../../../assets/img/theme/default-avatar.png'
import { toast } from 'react-toastify';
import AddServiceAccountModal from "./AddServiceAccountModal";
import ConfirmDeleteServiceAccountModal from "./ConfirmDeleteServiceAccountModal";

const pricesData = ['tier 1', 'tier 2', 'tier 3', 'tier 3'].map((item, index) => {
  return {
    tier: index + 1,
    hour: ""
  }
})

class System extends React.Component {
  state = {
    userList: [],
    activeTab: '1',
    prices: pricesData,
    errors: {},
    serviceAccounts: [],
    showAddServiceAccountModal: false,
    showConfirmDeleteServiceAccountModal: false,
    deletingAccount: {}
  };

  toggle = tab => {
    if (tab !== this.state.activeTab) {
      this.setState({ activeTab: tab });
    }
  }

  checkHourTierValidate = () => {
    const { prices } = this.state;
    prices.forEach((item, index) => {
      const name = `tier_${index}_hour`;
      delete this.state.errors[name];
      const currentData = prices[index] && Number(prices[index]['hour']);
      const preData = prices[index - 1] && Number(prices[index - 1]['hour']);
      const nextData = prices[index + 1] && Number(prices[index + 1]['hour']);
      if (index === 0) {
        if (currentData && nextData && (currentData >= nextData)) {
          this.setState({ errors: { ...this.state.errors, [name]: ['Data should be less than next Tier'] } })
        }
      } else if (index < 3) {
        if (preData && currentData && (currentData <= preData)) {
          this.setState({ errors: { ...this.state.errors, [name]: ['Data should be lager than previous Tier'] } })
        } else if (currentData && nextData && (currentData >= nextData)) {
          this.setState({ errors: { ...this.state.errors, [name]: ['Data should be less than next Tier'] } })
        }
      } else {
        if (preData && currentData && (currentData <= preData)) {
          this.setState({ errors: { ...this.state.errors, [name]: ['Data should be lager than previous Tier'] } })
        }
      }
    })
  }

  handleChangePricing = e => {
    const name = e.target.name;
    const value = Number(e.target.value);
    const index = name.split('_')[1];
    const type = name.split('_')[2];
    const { prices } = this.state;
    prices[index][type] = value;
    this.setState({ prices }, () => {
      if (type === 'hour') {
        this.checkHourTierValidate();
      }
    });
  }

  handleError = (field, status) => {
    let check = true;
    if (status) {
      for (let key in this.state.isFormReady) {
        check = check && this.state.isFormReady[key];
      }
    }
    this.setState(
      { isFormReady: { ...this.state.isFormReady, [field]: status, check: check && status } }
    );
  }

  getListAccount = () => {
    adminApi.getListAdmin().then(res => {
      this.setState({
        userList: res.result
      })
    })
  }

  getTierSetting = () => {
    systemSettingsApi.getTier().then(res => {
      const valueArr = res && res.value.split(',');
      const prices = this.state.prices.map((item, index) => {
        item.hour = valueArr[index];
        return item;
      });
      this.setState({ prices });
    })
  }

  getServiceAccounts() {
    serviceAccountApi.getList().then(res => {
      this.setState({serviceAccounts: res.result});
    });
  }

  deleteServiceAccount(id) {
    this.showConfirmDeleteServiceAccountModal(false);
    serviceAccountApi.delete(id).then(() => {
      toast.success("Service Account deleted successfully")
      this.getServiceAccounts();
    }).catch(() => toast.error("Couldn't delete Service Account"))
  }

  showAddServiceAccountModal(show) {
    this.setState({showAddServiceAccountModal: show});
  }

  showConfirmDeleteServiceAccountModal(show) {
    this.setState({showConfirmDeleteServiceAccountModal: show});
  }

  confirmDeleteAccount(account) {
    this.setState({deletingAccount: account});
    this.showConfirmDeleteServiceAccountModal(true);
  }

  addServiceAccount(data) {
    serviceAccountApi.add(data).then(() => {
      this.showAddServiceAccountModal(false);
      toast.success("Service Account added successfully")
      this.getServiceAccounts();
    }).catch(() => toast.error("Couldn't add Service Account"))
  }

  saveTierSetting = () => {
    const toHourString = this.state.prices.map(item => item.hour).join();
    systemSettingsApi.setTier({ value: toHourString }).then(res => {
      toast.success("Update tier setting successfully");
    }).catch(err => {
      const errors = err.response.data;
      this.setState({ errors });
      toast.error("Update tier setting failed");
    })
  }

  componentDidMount() {
    this.getListAccount();
    this.getTierSetting();
    this.getServiceAccounts();
  }

  render() {
    const { userList, activeTab, prices, errors, serviceAccounts } = this.state
    return (
      <Container className="mt-4 system" fluid >
        <Nav tabs>
          <NavItem>
            <NavLink
              className={classnames({ active: activeTab === '1' })}
              onClick={() => { this.toggle('1'); }}
            >
              Admin Accounts
                    </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: activeTab === '2' })}
              onClick={() => { this.toggle('2'); }}
            >
              Tier Setting
                    </NavLink>
          </NavItem>
        </Nav>
        <TabContent activeTab={activeTab}>
          <TabPane tabId="1">
            <Row className="d-flex justify-content-around mt-4">
              <Card>
                <CardHeader className="border-0 d-flex justify-content-center my-3">
                  <h2> System Admin Accounts</h2>
                </CardHeader>
                <Table
                  className="align-items-center table-flush"
                  responsive
                  striped
                >
                  <thead className="table-striped">
                    <tr>
                      <th className="w-80px" />
                      <th><strong>User</strong></th>
                      <th><strong>E-mail</strong></th>
                      <th />
                    </tr>
                  </thead>
                  <tbody>
                    {
                      userList && userList.map((elm, i) => {
                        const fullName = elm.firstName || elm.lastName ? `${elm.firstName} ${elm.lastName}` : "No Name"
                        return (
                          <tr key={i}>
                            <td className="table-user text-center">
                              <img
                                alt="..."
                                className="avatar avatar-sm rounded-circle "
                                src={elm.avatar || avatar}
                              />
                            </td>
                            <td>
                              <b> {fullName} </b>
                            </td>
                            <td>
                              <span className="text-muted">{elm.email}</span>
                            </td>

                            <td className="table-actions">
                              <Link to={`/admin/system/${elm.id}`} id={"user_" + elm.id} className="table-action">
                                <i className="fas fa-user-edit" />
                              </Link>
                              <UncontrolledTooltip delay={0} target={"user_" + elm.id}>
                                Edit {fullName}
                              </UncontrolledTooltip>
                            </td>
                          </tr>
                        )
                      })
                    }
                  </tbody>
                </Table>
                <div className="d-flex justify-content-center my-4">
                  <Link to={`/admin/system/create`}>
                    <Button color="success" >Add New</Button>
                  </Link>
                </div>
              </Card>
              <Card className="service-account-container">
               <CardHeader className="border-0 d-flex justify-content-center my-3">
                  <h2> Service Accounts</h2>
                </CardHeader>
                <Table
                  className="align-items-center table-flush"
                  responsive
                  striped
                >
                  <thead className="table-striped">
                    <tr>
                      <th><strong>Type</strong></th>
                      <th><strong>E-mail</strong></th>
                      <th />
                    </tr>
                  </thead>
                  <tbody>
                    {
                      serviceAccounts && serviceAccounts.map((elm, i) => {
                        const type = elm.isKiosk ? "Kiosk" : elm.isWelcomeBoard ? "Welcome Board" : "error"
                        return (
                          <tr key={i}>
                            <td>
                              <b> {type} </b>
                            </td>
                            <td>
                              <span className="text-muted">{elm.email}</span>
                            </td>

                            <td className="table-actions">
                              <button type="button" className="btn btn-danger" onClick={() => this.confirmDeleteAccount(elm)}><span className="fas fa-trash"></span> Delete</button>
                            </td>
                          </tr>
                        )
                      })
                    }
                  </tbody>
                  <tfoot>
                    <tr>
                      <td colSpan="3" className="text-center">
                        <button type="button" className="btn btn-success" onClick={() => this.showAddServiceAccountModal(true)}><span className="fas fa-plus"></span> Add service account</button>
                      </td>
                    </tr>
                  </tfoot>
                </Table>
              </Card>
            </Row>
          </TabPane>
          <TabPane tabId="2">
            <Row className="d-flex justify-content-center mt-4">
              <Card>
                <CardHeader className="border-0 d-flex justify-content-center my-3">
                  <h2> Tier settings</h2>
                </CardHeader>
                <Table
                  className="align-items-center table-flush"
                  responsive
                  striped
                >
                  <thead>
                    <tr>
                      <th width="20"><strong>Tier</strong></th>
                      <th width="30"><strong>From (Hrs)</strong></th>
                      <th width="50"><strong>To (Hrs)</strong></th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      prices.map((item, index) =>
                        <tr key={index}>
                          <td>
                            <div className="system__tier__item">{`Tier ${item.tier}`}</div>
                          </td>
                          <td>
                            <div className="system__tier__item justify-content-center">{index > 0 ? prices[index - 1].hour && (Number(prices[index - 1].hour) + 1) : 1}</div>
                          </td>
                          <td>
                            <FormInput regex="number" required={true} type="text" placeholder="Hours" name={`tier_${index}_hour`} defaultValue={item.hour} errors={errors[`tier_${index}_hour`]} onChange={this.handleChangePricing} handleError={this.handleError} />
                          </td>
                        </tr>)
                    }
                  </tbody>
                </Table>
                <div className="d-flex justify-content-center my-4">
                  <Button
                    color="success"
                    onClick={this.saveTierSetting}
                  >
                    Save Changes
                            </Button>
                </div>
              </Card>
            </Row>
          </TabPane>
        </TabContent>
        <AddServiceAccountModal isOpen={this.state.showAddServiceAccountModal} toggle={() => this.showAddServiceAccountModal(false)} onAdd={e => this.addServiceAccount(e)} />
        <ConfirmDeleteServiceAccountModal isOpen={this.state.showConfirmDeleteServiceAccountModal} toggle={() => this.showConfirmDeleteServiceAccountModal(false)} account={this.state.deletingAccount} onConfirm={() => this.deleteServiceAccount(this.state.deletingAccount.id)}/>
      </Container>
    );
  }
}

export default System;
