import React from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter, Button } from 'reactstrap';

function ConfirmDeleteServiceAccountModal(props) {
    return (
        <Modal isOpen={props.isOpen} toggle={() => props.toggle()}>
            <ModalHeader>Confirm action</ModalHeader>
            <ModalBody>
                Are you sure you want to delete service account {props.account.email}?
            </ModalBody>
            <ModalFooter>
                <Button color="link" onClick={e => props.toggle()}>Cancel</Button>
                <Button color="danger" onClick={e => props.onConfirm()}>
                    <i className="fas fa-trash" /> Delete
                </Button>
            </ModalFooter>
        </Modal>
    )
}

export default ConfirmDeleteServiceAccountModal;